#creator-container {
    height: auto;
    min-height: 1036px;
    width: 100%;
    background: #240046;
    display: flex;
    -webkit-box-pack: left;
    justify-content:  center;
    align-content: flex-start;
    flex-flow: row wrap;
    overflow: hidden;
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 110px;
 }
  
 .creator-title {
    font-size: 80px;
    color: white;
    font-weight: 600;
    margin-top: 45px;
    height: 100px;
    margin-bottom: 70px;
 }
  
 .text-controls {
    display: inline-block;
    margin-top: 92px;
    margin-left: auto;
    height: 53px;
 }
  
 .text-controls a {
    text-decoration: none;
    color: white;
    font-weight: 600;
    font-size: 20px;
    margin-left: 40px;
    width: auto;
}

.text-controls a:hover {
    text-decoration: underline;
}

 .top-text-labels {

    display: inline-block;
    width: 100%;
    max-height: 30px;
    margin-bottom: 50px;
 }
  
 .top-text-labels span {
    color: white;
    font-weight: 600;
    font-size: 26px;
    margin-left: 400px;
}

.instructions {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
    font-size: 18px;
}
 
.browser-main {
    position:fixed;
    background: #5A189A;
    width: 80%;
    height: 72vh;
    border-radius: 30px;
    top:50%;
    left:50%;
    padding-right: 65px;
    padding-left: 65px;
    transform: translate(-50%,-50%);
 }
  
 .browser-positioner {
    position: relative;
    width: 100%;
    height: 100%;
 }
  
 .browser-title {
    font-size: 64px;
    color: white;
    font-weight: 600;
    margin-top: 40px;
    margin-bottom: 25px;
 }
  
 .browser-close-button {
    position: absolute;
    cursor: pointer;
    width: 16px;
    height: 16px;
    right: -20px;
    top: 0;
 }
  
 .browser-search {
    position: absolute;
    top: 24px;
    left: 550px;
    color: white;
 }
  
 .browser-table {
    width: 100%;
    color: white;
    border-collapse: collapse;
 }
  
 .table-header {
    font-size: 22px;
    font-weight: 600;
    padding-bottom: 10px;
    border-bottom: 1px solid #410D74;
 }
  
 .table-header .header-cell {
    display: inline-block;
    width: 20vw;
 }
  
 .table-header .header-cell:last-child {
    width: calc(100% - 40vw);
 }
  
 .table-row .table-cell {
    display: inline-block;
    vertical-align: top;
    width: 20vw;
    font-size: 16px;
    padding: 10px;
    font-weight: 400;
 }
  
 .table-row .table-cell:last-child {
    width: calc(100% - 40vw); 
 }
  
 .table-row:hover {
    background: #4A1480;
 }
  
 label.Mui-focused {
    color: 'white';
    font-family: "futura-pt"
 }
  
 .MuiFormLabel-root {
    color: 'white';
    font-family: "futura-pt"
 }
  
 .MuiInputBase-root {
    color: 'white',
 }
  
 .MuiInput-underline:before {
    border-bottom-color: 'white',
 }
  
 .MuiInput-underline:after {
    border-bottom-color: 'white',
 }
 .dropbtn {
    background-color: purple;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
  }
  .dropdown {
    position: relative;
    display: inline-block;
  }
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  .dropdown-content a:hover {background-color: #ddd;}
  .dropdown:hover .dropdown-content {display: block;}
  .dropdown:hover .dropbtn {background-color:pink;}
 
 .footer {
     background: #451179;
     width: 100%;
     height: 180px;
     text-align: center;
     font-size: 20px;
     padding-top: 55px;
 }

.footer-break {
    width: 100%;
    height: 20px;
}

 .footer a {
     color: white;
     font-weight: 600;
 }

 .footer a:hover {
    text-decoration: underline;
}